.logoWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  color: #008000;
}
.logoWrap img {
  filter: brightness(200) invert(1);
}
.logoWrap h1 {
  font-size: 3em;
  color: #292929;
}
header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #fff;
  padding: 15px;
  /* background: #008000; */
  border-top: 1px solid #292929;
  border-bottom: 1px solid #292929;
}
span {
  color: #ffa500;
}
header a {
  text-decoration: none;
  font-size: 1.4em;
  margin: 5px 15px;
  color: #696969;
}
header a:hover {
  font-weight: 600;
  border-top: 3px solid #292929;
  padding-top: 2%;
}
.active {
  font-weight: 600;
  border-top: 3px solid #292929;
  padding-top: 2%;
}
.navLinks {
  /* margin-top: 15px; */
  align-items: center;
}
.dest {
  color: #008000;
  font-weight: 600;
}
.dest ul {
  display: none;
  position: absolute;
  /* right: 34%; */
  width: 150px;
  background: #fff;
  border-radius: 0px 0px 4px 4px;
  align-items: center;
  text-align: left;
  padding-top: 15px;
  padding-bottom: 5px;
  /* margin: 5px 15px; */
  z-index: 10;
  border-bottom: 1px solid #292929;
}
.dest:hover ul {
  display: block;
}
.navLinks ul li {
  list-style: none;
}
.navLinks ul li a {
  font-size: 0.7em;
  transition: 0.4s ease-in-out;
}
.navLinks ul li:hover a {
  color: #fff;
  border-top: none;
  padding-top: 0;
}
.navLinks ul li:hover {
  background: orange;
  /* border-top: 1px solid #292929; */
}
.blog {
  border-top: none;
  color: #ffa500;
  border: 1px solid orange;
  padding: 2px 7px;
  border-radius: 20px;
  font-size: 1em;
  transition: 0.5s;
}
.blog:hover {
  border: 1px solid orange;
  padding: 2px 7px;
  background: #ffa500;
  color: #fff;
}
/* .blogBtn {
  padding: 3px 7px;
  margin-bottom: 55px;
} */

.userSigning {
  display: flex;
  position: absolute;
  right: 5%;
  align-items: center;
}
.userSigning p {
  margin: -20px;
}
.userSigning button {
  font-size: 0.8em;
  border: none;
  background: transparent;
  color: #696969;
}
.userSigning button:hover {
  cursor: pointer;
  text-decoration: underline;
}
.userSigning a:hover {
  border-top: none;
}
.signUp:hover {
  color: #ffa500;
}
.signIn:hover {
  color: #0bd30b;
}
.signLine {
  border: 1px solid #696969;
  height: 20px;
  margin: 0 10px;
}

.userLogOut {
  position: absolute;
  right: 50px;
}

.userLogOut button {
  font-size: 0.8em;
  font-weight: 600;
  border: none;
  background: transparent;
  color: #696969;
  color: red;
}

.userLogOut button:hover {
  cursor: pointer;
  text-decoration: underline;
}

.userLogOut a:hover {
  border-top: none;
}

.regUsers {
  position: absolute;
  left: 50px;
}

.regUsers button {
  font-size: 0.8em;
  font-weight: 600;
  border: none;
  background: transparent;
  color: #696969;
  color: orange;
}

.regUsers button:hover {
  cursor: pointer;
  text-decoration: underline;
}

.regUsers a:hover {
  border-top: none;
}
